import React, { ReactNode } from "react";
import { useExtensionContext } from "../../hooks/useExtensionContext";
import { createGlobalStyle, ThemeProvider } from "styled-components";

const themes = {
  vscode: {
    colors: {
      activeSelectionBackground: "var(--vscode-list-activeSelectionBackground)",
      inactiveSelectionBackground:
        "var(--vscode-list-inactiveSelectionBackground)",
      editorForeground: "var(--vscode-editor-foreground)",
      editorBackground: "var(--vscode-editor-background)",
      editorErrorForeground: "var(--vscode-editorError-foreground)",
      placeholderForeground: "var(--vscode-input-placeholderForeground)",
      focusHighlightForeground: "var(--vscode-list-focusHighlightForeground)",
      black: "var(--vscode-terminal-ansiBlack)",
      panelBackground: "var(--vscode-panel-background)",
      codeBlockBackground: "rgb(30, 30, 30)",
      linkColor: "var(--vscode-textLink-foreground)",
    },
    font: {
      bodySize: "var(--vscode-font-size)",
    },
  },
  ij: {
    colors: {
      activeSelectionBackground: "#323232",
      inactiveSelectionBackground: "#37373d",
      editorForeground: "#cccccc",
      editorBackground: "#1f1f1f",
      editorErrorForeground: "#f14c4c",
      placeholderForeground: "#ffffff78",
      focusHighlightForeground: "#2aaaff",
      black: "#000000",
      panelBackground: "#181818",
      codeBlockBackground: "rgb(30, 30, 30)",
      linkColor: "#40a6ff",
    },
    font: {
      bodySize: "13px",
    },
  },
};

const GlobalStyle = createGlobalStyle`
  a {
    color: ${({ theme }) => theme.colors.linkColor};
    text-decoration: none;
  }

  body {
    font-size: ${({ theme }) => theme.font.bodySize};
  }
`;

export function ThemeIdeProvider({ children }: { children: ReactNode }) {
  const extensionContext = useExtensionContext();
  return (
    <ThemeProvider
      theme={extensionContext ? themes[extensionContext.ide] : themes.ij}
    >
      <GlobalStyle />
      {children}
    </ThemeProvider>
  );
}
