import {
  QueryFunction,
  useMutation,
  useQuery,
  UseQueryResult,
} from "react-query";
import { sendRequestToExtension } from "../components/communication/ExtensionCommunicationProvider";
import { ChatConversation, ChatState } from "../types/ChatTypes";
import Events from "../utils/events";
import queryClient from "../utils/queryClient";
import { useEffect, useState } from "react";
import { ExtensionContext } from "./useExtensionContext";

const GET_CHAT_DATA_KEY = "GET_CHAT_DATA_KEY";
const INIT_APP_KEY = "INIT_APP_KEY";

export function useGetChatData(): UseQueryResult<ChatState> {
  return useWaitForQueryResponse(
    GET_CHAT_DATA_KEY,
    async () =>
      await sendRequestToExtension<void, ChatState>({
        command: "get_chat_state",
      })
  );
}

export function useUpdateConversationData() {
  return useMutation({
    async mutationFn(updatedConversation: ChatConversation) {
      await sendRequestToExtension<ChatConversation, void>({
        command: "update_chat_conversation",
        data: updatedConversation,
      });
    },
    onSuccess() {
      queryClient.invalidateQueries({ queryKey: [GET_CHAT_DATA_KEY] });
    },
  });
}

export function useClearChatData() {
  const { data: chatData } = useGetChatData();
  return useMutation({
    async mutationFn() {
      if (chatData) {
        Events.sendUserClearedAllConversationsEvent(chatData);
      }
      await sendRequestToExtension<void, void>({
        command: "clear_all_chat_conversations",
      });
    },
    onSuccess() {
      queryClient.invalidateQueries({ queryKey: [GET_CHAT_DATA_KEY] });
    },
  });
}

export function useInitApp(): UseQueryResult<ExtensionContext> {
  return useWaitForQueryResponse(
    INIT_APP_KEY,
    async () =>
      await sendRequestToExtension<void, ExtensionContext>({
        command: "init",
      })
  );
}

function useWaitForQueryResponse<TData, TError>(
  key: string,
  queryFn: QueryFunction<TData>
): UseQueryResult<TData, TError> {
  const [refetchInterval, setRefetchInterval] = useState<number | false>(500);

  const queryInfo = useQuery<TData, TError>(key, queryFn, {
    refetchInterval,
  });

  useEffect(() => {
    if (queryInfo.data) {
      setRefetchInterval(false);
    }
  }, [queryInfo.data]);

  return queryInfo;
}
