declare global {
  interface Window {
    acquireVsCodeApi?(): ExtensionApi;
    postPluginMessage?(msg: unknown): void;
  }
}

export interface ExtensionApi {
  postMessage(msg: unknown): void;
  setState?(state: unknown): void;
  getState?(): unknown;
}

const vscodeApi = window.acquireVsCodeApi && window.acquireVsCodeApi();

const intellijApi = {
  postMessage: (msg: unknown): void =>
    window.postPluginMessage && window.postPluginMessage(msg),
};

export function getExtensionApi(): ExtensionApi | undefined {
  if (vscodeApi) {
    return vscodeApi;
  } else if (window.postPluginMessage) {
    return intellijApi;
  }
}
