import constate from "constate";
import { useEffect, useState } from "react";
import { useInitApp } from "./chatData";

export type ExtensionContext = {
  ide: "ij" | "vscode";
};

function useCreateExtensionContext(): ExtensionContext | undefined {
  const [extensionContext, setExtensionContext] = useState<
    ExtensionContext | undefined
  >();
  const { data: initResponse } = useInitApp();

  useEffect(() => {
    setExtensionContext(initResponse);
  }, [initResponse]);

  return extensionContext;
}

const [ExtensionContextProvider, useExtensionContext] = constate(
  useCreateExtensionContext
);

export { ExtensionContextProvider, useExtensionContext };
