import React from "react";
import styled from "styled-components";
import { useChatState } from "../../hooks/useChatState";

export function IntellijNavigator(): React.ReactElement {
  const { resetCurrentConversation, createNewConversation, clearMessages } =
    useChatState();
  return (
    <Wrapper>
      <Button onClick={resetCurrentConversation}>Back to History</Button>
      <Button onClick={clearMessages}>Clear Messages</Button>
      <Button onClick={createNewConversation}>New Conversation</Button>
    </Wrapper>
  );
}

const Wrapper = styled.div`
  padding: 0.5rem 0.6rem;
  display: flex;
  align-items: center;
  border-bottom: solid 1px
    ${({ theme }) => theme.colors.activeSelectionBackground};

  > *:not(:last-child) {
    margin-right: 1.5rem;
  }
`;

const Button = styled.div`
  color: ${({ theme }) => theme.colors.editorForeground};
  transition: all 0.1s ease;
  cursor: pointer;

  &:hover {
    color: ${({ theme }) => theme.colors.focusHighlightForeground};
  }
`;
